<template>
  <div>
    <div class="container p-0">
      <div>
        <swiper :options="swiperOption" class="">
          <swiper-slide v-for="item in filterData" :key="item.id">
            <ItemCard :card-data="item" :loadingStatus="status" @addtoCart="addtoCart"></ItemCard>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import ItemCard from '@/components/ItemCard'

export default {
  name: 'home',
  components: {
    ItemCard
  },
  data () {
    return {
      swiperOption: {
        spaceBetween: 10, // 在slide之间设置距离（单位px）。
        centeredSlides: false, // 设定为true时，active slide会居中，而不是默认状态下的居左。
        grabCursor: true, // 设置为true时，鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状。（根据浏览器形状有所不同）
        pagination: {
          el: '.swiper-pagination',
          clickable: true // 此参数设置为true时，点击分页器的指示点分页器会控制Swiper切换
        },
        autoplay: {
          delay: 4000
        },
        slidesPerView: 4, // 设置slider容器能够同时显示的slides数量(carousel模式)。
        slidesPerGroup: 4, // 在carousel mode下定义slides的数量多少为一组。
        breakpoints: {
          480: {
            slidesPerView: 1,
            slidesPerGroup: 1,
            spaceBetween: 20
          },
          767: {
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 10
          },
          992: {
            slidesPerView: 3,
            slidesPerGroup: 3,
            spaceBetween: 20
          }
        }
      },
      products: [],
      status: {
        loadingItem: ''
      }
    }
  },
  props: {
    card: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  methods: {
    getProducts () {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`
      vm.isLoading = true
      this.$http.get(url).then(response => {
        vm.products = response.data.products
        console.log(response)
        vm.isLoading = false
      })
    },
    addtoCart (id, qty = 1) {
      const vm = this
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.status.loadingItem = id
      const cart = {
        product_id: id,
        qty
      }
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response)
        vm.status.loadingItem = ''
        if (response.data.success) {
          this.$bus.$emit('message:push', response.data.message, 'success')
        } else {
          this.$bus.$emit('message:push', response.data.message, 'danger')
        }
      })
    }
  },
  computed: {
    filterData () {
      const vm = this
      console.log(vm.card.category)
      if (!vm.card.category) {
        return vm.products
      } else {
        return vm.products.filter((item) => {
          return item.category === vm.card.category
        })
      }
    }
  },
  created () {
    this.getProducts()
  }
}
</script>

<style lang="scss" scoped>
.swiper-container{
  min-height: 380px;
}
</style>
